    -webkit-filter: drop-shadow( 0px 0px 0px #fff) drop-shadow( 7px -7px 4px #FAECDA) drop-shadow( 13px 0px 10px #FAECDA);
    filter: drop-shadow( 0px 0px 0px #fff) drop-shadow( 7px -7px 4px #FAECDA) drop-shadow( 13px 0px 10px #FAECDA);
  }

  to {
    -webkit-filter: drop-shadow( 5px 0px 3px #fff) drop-shadow( 10px -10px 7px #FAECDA) drop-shadow( 13px 0px 19px #FAECDA);
    filter: drop-shadow( 5px 0px 3px #fff) drop-shadow( 10px -10px 7px #FAECDA) drop-shadow( 13px 0px 19px #FAECDA);
  }
}

@keyframes svg-shadow {
  from {
    -webkit-filter: drop-shadow( 0px 0px 0px #fff) drop-shadow( 7px -7px 4px #FAECDA) drop-shadow( 13px 0px 10px #FAECDA);
    filter: drop-shadow( 0px 0px 0px #fff) drop-shadow( 7px -7px 4px #FAECDA) drop-shadow( 13px 0px 10px #FAECDA);
  }

  to {
    -webkit-filter: drop-shadow( 5px 0px 3px #fff) drop-shadow( 10px -10px 7px #FAECDA) drop-shadow( 13px 0px 19px #FAECDA);
    filter: drop-shadow( 5px 0px 3px #fff) drop-shadow( 10px -10px 7px #FAECDA) drop-shadow( 13px 0px 19px #FAECDA);
  }
}

